import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ResidentFormComponent } from './resident-form/resident-form.component';
import { ResidentsListComponent } from './residents-list/residents-list.component';
import { ResidentsComponent } from './residents.component';

const routes: Routes = [
	{
		path: '',
		component: ResidentsComponent,
		children: [
			{
				path: '',
				component: ResidentsListComponent
			},
			{
				path: 'crear',
				component: ResidentFormComponent
			},
			{
				path: 'editar',
				component: ResidentFormComponent
			}
		]
	}
];

@NgModule({
	imports: [
		RouterModule.forChild(routes)
	],
	exports: [
		RouterModule
	]
})
export class ResidentsRoutingModule {
}
